@import 'general';
@import 'variables';
@import 'mixins';
@import 'keyframes';

// ! PAGE LAYOUT
.content__section {
  &--shuqqIntro {
    display: block;
  }
  &--shopLinks {
    @include grid(1fr 1fr 1fr, $space-m, none, 0);
    @media screen and (max-width: $breakpoint-s) {
      @include grid(1fr, 0, auto, $space-m);
    }
  }
  &--releaseBoxes {
    @include grid(1fr, 0, auto, $space-s);
  }
}

// ! SECTION LAYOUT
// ! Music Intro Section
.shuqqIntro {
  &__image {
    width: 100%;
    margin-bottom: $space-xs;
    filter: drop-shadow(0 2px 3px rgba(0, 0, 0, 0.25));
  }
  &__text {
    @include columns(2, $space-m, 0.01rem solid $mid1-color);
    text-align: justify;
    line-height: $line-height;
    @media screen and (max-width: $breakpoint-m) {
      @include columns(2, $space-m, 0.01rem solid $mid1-color);
    }
    @media screen and (max-width: $breakpoint-s) {
      @include columns(1, 0, 0 solid $mid2-color);
    }
  }
}

// ! Music Shop Link Section
.shopLink {
  @include flex(row nowrap, center, center, center);
  width: 100%;
  opacity: 0.25;
  background: $bright-color;
  padding: $space-s;
  border-radius: $border-radius;
  &:hover {
    opacity: 0.75 !important;
  }
  img {
    height: 2rem;
    filter: drop-shadow(0 2px 3px rgba(0, 0, 0, 0.25));
  }
}

// ! Music Release Box Section
.releaseBox {
  @include boxShadow--std(0.1);
  @include grid(250px 1fr, 0, 250px, 0);
  border-radius: 0 0 1rem 1rem;
  justify-self: center;
  max-width: 65%;
  @media screen and (max-width: $breakpoint-s) {
    @include grid(1fr, 0, auto, 0);
    max-width: 100%;
    border-radius: 0;
  }
  &__player {
    @include boxShadow--std(0.25);
    grid-column: 2 / 3;
    grid-row: 1 / 2;
    height: 250px;
    justify-self: center;
    position: relative;
    width: 100%;
    z-index: 13;
    @media screen and (max-width: $breakpoint-s) {
      grid-column: 1 / 2;
      grid-row: 2 / 3;
      // height: 50vh;
    }
  }
  &__cover {
    @include boxShadow--std(0.25);
    grid-column: 1 / 2;
    grid-row: 1/ 2;
    justify-self: center;
    position: relative;
    height: 250px;
    width: 100%;
    z-index: 13;
    @media screen and (max-width: $breakpoint-s) {
      grid-column: 1 / 2;
      grid-row: 1 / 2;
      height: 350px;
    }
  }
  &__data,
  &__description {
    background: $dark-color;
    color: $bright-color;
    padding: $space-s;
    @media screen and (max-width: $breakpoint-s) {
      padding: $space-s $space-m;
    }
  }
  &__data {
    // border-bottom: 1px dashed $mid2-color;
    border-radius: 0 0 1rem 1rem;
    grid-column: 1 / 3;
    grid-row: 2 / 3;
    @media screen and (max-width: $breakpoint-s) {
      grid-column: 1 / 2;
      grid-row: 3 / 4;
    }
    table {
      width: 100%;
      font-size: 0.85rem;
    }
    .data {
      &__label {
        font-weight: bold;
        padding-right: $space-s;
        text-align: right;
        width: 50%;
      }
      &__content {
        padding-left: $space-s;
      }
    }
  }
}
